h1 {

    font-family: fatfrank, sans-serif;

    font-weight: 400;
    
    font-style: normal;
}

h2 {
    font-family: fatfrank, sans-serif;

    font-weight: 400;
    
    font-style: normal;
    font-size: medium;
    text-transform: uppercase;
    margin: 0px;
}

.subtitle {
    margin-top: -5px;
margin-bottom:20px;
}

body {
    font-size: large;
    margin:0px;
}

.title {
    text-align: left;
    font-size: 48px;
    display: inline-block;
    position: fixed;

    top: 0px;
    width:100%;
    z-index: 100;
    /*background-color: white;*/
      content: "";
  pointer-events: none;
  background-image: linear-gradient(to bottom, rgba(255,255,255,1),  rgba(255,255,255,1) 92%, rgba(255,255,255, 0) 100%);
}

.title h1 {
    margin: 0px;
    margin-bottom: -10px;
    margin-left: 20px

}

.rodal-mask {
    background: rgba(0, 0, 0, 0.1);
}

.round-button {
    display:block;
    width:80px;
    height:80px;
    line-height:80px;
    border: 2px solid #f5f5f5;
    border-radius: 50%;
    color:#f5f5f5;
