}

.title.hidden {
    text-align: center;
    visibility: hidden;
    font-size: 48px;
    display: inline-block;
    position: relative;
    margin-bottom: 50px;
    top: 0px;
    z-index: 100;
    /*background-color: white;*/
      content: "";
  pointer-events: none;
  background-image: linear-gradient(to bottom, rgba(255,255,255,1), rgba(255,255,255, 0) 90%);
  padding-left: 10px
}




@media only screen and (max-width: 527px) {
    .title {
