    height: 100vh;
    z-index: 200;
    pointer-events: none;
}

.card, .rodal-dialog {
    width: 50vw;
    min-height: 40vh;
    border-radius: 10px;
    padding: 20px;
    margin-top: 50px;
    margin-bottom: 50px;
    box-shadow: 0 2px 4px 0 rgba(0, 1, 0, 0.3), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
   animation-duration: 8s !important;
    -webkit-animation: wiggle 8s ease-in-out infinite;
	-moz-animation: wiggle 8s ease-in-out infinite;
	animation: wiggle 8s ease-in-out infinite;
}

textarea {
    display: inline-flex;
width: 100%;
height: 30vh;
font-size: large;
}

.rodal-dialog {
    width: 60vw;
}

.background-transparent {
    background-color: transparent;

}
.background-red {
    background-color: red;
    
}

input[type="text"] {
    width: 100%;
    font-size: xx-large;
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: fatfrank, sans-serif;
    font-size: 2em;
    font-weight: 400;
    font-style: normal;
    border-width:0px;
    border-bottom:3px solid #000;
    margin-top: 20px;
    margin-bottom:20px;

}

input[type="radio"] {
    display:none;
}

button {
    padding: 5px 20px 5px 20px; /* Making space around letters */
    font-family: fatfrank, sans-serif;

    font-weight: 400;
    
    font-style: normal;
    font-size: medium;
    text-transform: uppercase;
    border-color: transparent;
    background-color: transparent;


}

.material-icons {
    vertical-align: bottom;
}

.selected {
    border-bottom:3px solid #000;
}
button:focus {outline:0;}


.toolbar {
    text-align: left;
    font-size: 48px;
    pointer-events: auto;
    margin: 0px;
    display: flex;
    overflow-x: scroll;
    background-color: transparent;
    margin-left: 20px;
}

.toolbar button {
    padding-left: 0px;
    margin-right: 20px;
    margin-left: -1px;
    padding: 0px;
}

label {
    padding: 5px 20px 5px 20px; /* Making space around letters */
    font-family: fatfrank, sans-serif;

    font-weight: 400;
    
    font-style: normal;
    font-size: medium;
    text-transform: uppercase;
 
}


input[type="radio"]:checked + label {
    background-color: black; /* Changing background color */
    color:white;
    border-radius: 10px; /* Making border radius */
    padding: 5px 20px 5px 20px; /* Making space around letters */
  }

  .typeselector {
    font-size: large;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    height: 60px;
    padding-top: 30px;
    border-radius: 2px;
    margin-top: 20px;
    margin-bottom: 20px;
    box-shadow: inset 0 10px 0px 0px white, inset 0 -10px 0px 0px white, inset 10px 0px 10px -10px rgba(0, 0, 0, 0.45), inset -10px 0px 10px -10px rgba(0, 0, 0, 0.45);

  }

  ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color:lightgray;
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: lightgray;
  }
  
  ::-ms-input-placeholder { /* Microsoft Edge */
    color: lightgray;
  }

  .typeradio {
    display: inline-block;
    height: 20px;
  }

/* safari and chrome */
@-webkit-keyframes wiggle {
	0% {-webkit-transform:rotate(1.5deg);}
	50% {-webkit-transform:rotate(-1.5deg);}
	100% {-webkit-transform:rotate(1.5deg);}
}

/* firefox */
@-moz-keyframes wiggle {
	0% {-moz-transform:rotate(1.5deg);}
	50% {-moz-transform:rotate(-1.5deg);}
	100% {-moz-transform:rotate(1.5deg);}
}

/* anyone brave enough to implement the ideal method */
@keyframes wiggle {
	0% {transform:rotate(1.5deg);}
	50% {transform:rotate(-1.5deg);}
	100% {transform:rotate(1.5deg);}
}

.card h1 {
    margin-top: 5px;
    margin-bottom: 10px;
}

.card:nth-child(odd) {
    margin-left: 15vw;
    transform: rotate(-4deg);
    float: left;
    /*-webkit-box-shadow: 0px 8px 6px -6px black;
    -moz-box-shadow: -px 8px 6px -6px black;
         box-shadow: -6px 8px 6px -6px black; */
}

.card:nth-child(even) {
    margin-right: 15vw;
    transform: rotate(4deg);
    float:right;
    /* -webkit-box-shadow: 6px 8px 6px -6px black;
    -moz-box-shadow: 6px 8px 6px -6px black;
         box-shadow: 6px 8px 6px -6px black;*/
}

a:-webkit-any-link {
    color:inherit;
}

@media only screen and (max-width: 800px) {
    .card, .rodal-dialog {
        width: 80vw;
        min-height: 20vh;
    }
    .card:nth-child(odd) {
        margin-left: 5vw;
        transform: rotate(-3deg);
    }
    .card:nth-child(even) {
        margin-left: 5vw;
        float: left;
        transform: rotate(3deg);
    }
    .card {
        margin-top: 25px;
        margin-bottom: 25px;
    }
    .title {
        text-align: center;
       
    }

    .title h1 {
        margin-left: 0px;
    }

    .toolbar {
        display:inline-block;
        text-align: center;
        margin-top:-10px;
        margin-left:0px;
    }

}


.rodal-dialog {
    overflow:auto;
    padding: 40px;
}
 

.grid-container {
    display: grid;
  }

button:hover, .typeradio:hover {
      /* Start the shake animation and make the animation last for 0.5 seconds */
  animation: shake 0.3s;

  /* When the animation is finished, start again */
  animation-iteration-count: infinite;
}
  @keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
  }